import Vue from "vue";
import Router from "vue-router";
import Home from "./views/home/Home.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      redirect: "/login"
    },
    {
      path: "/home",
      component: Home,
      children: [
        {
          path: "/",
          redirect: "index"
        },
        {
          path: "index",
          name: "index",
          component: () => import("./views/home/Index.vue")
        },
        {
          path: "homework",
          name: "homework",
          component: () => import("./views/home/Homework.vue")
        },
        {
          path: "settings",
          name: "settings",
          component: () => import("./views/home/Settings.vue")
        },
        {
          path: "help",
          name: "help",
          component: () => import("./views/home/Faq.vue")
        },
        {
          path: "/home/homework/:hid",
          name: "homeworkDetail",
          component: () => import("./views/home/homeworkDetail.vue"),
          props: true
        }
      ]
    },
    {
      path: "/about",
      name: "about",
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: "about" */ "./views/About.vue")
    },
    {
      path: "/login",
      name: "login",
      component: () => import("./views/Login.vue")
    },
    {
      path: "/teacherHome",
      component: () => import("./views/teacher/Home.vue"),
      children: [
        {
          path: "/",
          redirect: "index"
        },
        {
          path: "index",
          name: "teacherIndex",
          component: () => import("./views/teacher/Index.vue")
        },
        {
          path: "settings",
          name: "teacherSettings",
          component: () => import("./views/teacher/Settings.vue")
        },
        {
          path: "help",
          name: "teacherHelp",
          component: () => import("./views/teacher/FAQ.vue")
        },
        {
          path: "homeworks/:hid",
          name: "teacherHomework",
          component: () => import("./views/teacher/Homework.vue"),
          props: true
        },
        {
          path: "publish",
          name: "publish",
          component: () => import("./views/teacher/Publish.vue")
        }
      ]
    },
    {
      path: "/500",
      name: "500",
      component: () => import("./views/500.vue")
    },
    {
      path: "/404",
      name: "404",
      component: () => import("./views/404.vue")
    },
    {
      path: "/*",
      redirect: "/404"
    }
  ]
});
