import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const UPDATE_USER = "UPDATE_USER";
const UPDATE_TOKEN = "UPDATE_TOKEN";

export default new Vuex.Store({
  state: {
    user: null,
    token: null
  },
  mutations: {
    [UPDATE_USER](state, user) {
      state.user = user;
    },
    [UPDATE_TOKEN](state, tok) {
      state.token = tok;
    }
  },
  actions: {
    async updateUser() {},
    async updateToken() {}
  }
});
